@import 'Vars';

.TopBar{
    background:   linear-gradient(to right, $MainTiltColor , $MainBackgroundColor);
    height: 4rem;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    z-index: 100;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .LogoContainerTop{
        display: inline-block;
        height: 4rem;
        width: 4rem;

        .LogoTopBar{
            display: inline-block;
            height: 4rem;
            width: 4rem;
            padding: 0px;
            background-image : url('../imgs/logo.png');
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }

    .backToIcon{
        position: absolute;
        top: 0px;
        left: 0px;
        padding-top: 1rem;
        padding-left: 1.2rem;
        color: $TiltTextColor;
    }

    .deleteColIcon{
        position: absolute;
        right: 1rem;
        top: 1.2rem;
        color: $TiltTextColor;
    }

    .LogoutIcon{
        position: absolute;
        right: 1rem;
        top: 1.2rem;
        color: $TiltTextColor;
    }

}