@import 'Vars';

.ColeccionesPage{
 
    .AddColForm{
        background-color: rgba(0, 0, 0, 0.7);
        position: fixed;
        z-index: 100;
        width: 100%;
        height: 100%;
        top: 0px;
        left: 0px;
        text-align: center;
        padding-top: 15rem;

        .ContainerForm{
            background-color: $TiltTextColor;
            margin: 0px 2rem;
            padding: 1rem;
            border-radius: 4px;
            font-family: 'Roboto', sans-serif;

            .NameColInput{
                padding: 0rem 0rem 1rem 0rem;

                input{
                    width: calc(100% - 0.5rem);
                    height: 2.5rem;
                    font-size: 1rem;
                    text-align: center;
                    border-color: #85696957;
                    border-radius: 4px;
                    box-shadow: 2px 2px 5px #999;
                }
            }

            .AddNewColButton{
                font-size: 1rem;
                background:   linear-gradient(to right, $MainTiltColor , $MainBackgroundColor);
                color: $TiltTextColor;
                padding: 1rem;
                border-color: #85696957;
                border-radius: 4px;
                box-shadow: 2px 2px 5px #999;
            }

            .CancelColButton{
                margin-top: 0.5rem;
                font-size: 1rem;
                background-color: #333;
                color: $TiltTextColor;
                padding: 1rem;
                border-color: #85696957;
                border-radius: 4px;
                box-shadow: 2px 2px 5px #999;
            }
        }
    }


    .AddColButton{
        display: block;
        position: fixed;
        right: 3rem;
        bottom: 3rem;
        padding: 1rem;
        background:   linear-gradient(to right, $MainTiltColor , $MainBackgroundColor);
        color: $TiltTextColor;
        border-radius: 50%;
        cursor: pointer;
        i{
            font-size: 2rem !important;
        }
    }

    .Title{
        margin-left: 1rem;
        font-family: 'Playfair Display', serif;
        text-align: center;
        color: $MainBackgroundColor;
    }

    .CItem{
        padding: 1.5rem 1rem;
        margin: 1rem;
        background-color: $MainBackgroundColor;
        border-radius: 4px;
        color: $TiltTextColor;
        font-size: 1.2rem;
        position: relative;
        cursor: pointer;
        font-family: 'Roboto', sans-serif;

        .Desc{
            display: inline-block;
            vertical-align: middle;
        }

        .Icon{
            
            display: inline-block;
            position: absolute;
            right: 10px;
            vertical-align: middle;
        }
    }

}