@import 'Vars';
.LoginPage{
    .LogoContainer{
        text-align: center;
        padding-top: 4rem;

        .background{
            display: inline-block;
            background: linear-gradient(to right, $MainTiltColor , $MainBackgroundColor);
            height: 5rem;
            width: 5rem;
            border-radius: 100%;
        .Logo{
            display: inline-block;
            height: 4rem;
            width: 4rem;
            margin-top: 0.5rem;
            background-image : url('../imgs/icons8-usuario-96.png');
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            
        }
    }
    }
    
    .FormContainer{
        text-align: center;
        
        .LoginForm{
            display: inline-block;
            
            .FieldContainer{
                padding: 0.6rem;
    
                input{
                    padding: 0.7rem;
                    width: calc(100% - 1.9rem);
                    text-align: center;
                    border-color: #85696957;
                    border-radius: 5px;
                    box-shadow: 2px 2px 5px #999;
                }
    
                .Button{
                    background:   linear-gradient(to right, $MainTiltColor , $MainBackgroundColor);
                    color: #FFF;
                    padding: 1rem;
                    border-color: #85696957;
                    border-radius: 5px;
                    box-shadow: 2px 2px 5px #999;
                }
            }
    
            .ErrorMessage{
                color: #a00303;
                font-size: 0.7rem;
                font-weight: bold;
            }
        }
    }
}