@import 'Vars';
@import 'LoadingAxios';

.ColeccionPage{
    position: relative;

    .LoadingAxios{
        background-color: rgba(0, 0, 0, 0.7);
        position:fixed;
        z-index: 100;
        width: 100%;
        height: 100%;
        top: 0px;
        left:0px;
        text-align: center;
        padding-top: 20rem;

        .LoadingText{
            color: $TiltTextColor;
            padding-top: 1rem;
            font-size: 20px;
        }
    }

    .Title{
        margin-left: 1rem;
        font-family: 'Playfair Display', serif;
        text-align: center;
        color: $MainTextColor;
    }
    
    .PhotosContainer{
        margin: 0px 1rem;
        
        .FItem{
            width: 100%;
            height: 15rem;
            margin-bottom: 1rem;
            background-size: cover;
            border-style: solid;
            border-color: #85696957;
            border-radius: 5px;
            box-shadow: 2px 2px 5px #999;
            position: relative;

            .inPhotoIcon{
                background-color: rgba(0, 0, 0, 0.705);
                padding: 0.3rem;
                border-radius: 4px;
                display: inline-block;
                position: absolute;
                right: 0.5rem;
                top: 0.5rem;

                i{
                    color: $TiltTextColor;
                }
            }
        }
    }

    .AddPhoto{
        display: block;
        position: fixed;
        right: 3rem;
        bottom: 3rem;
        padding: 1rem;
        background:   linear-gradient(to right, $MainBackgroundColor , $MainTiltColor);
        color: $TiltTextColor;
        border-radius: 50%;
        cursor: pointer;
        z-index: 100;
        i{
            font-size: 2rem !important;
        }
    }
}